angular.module('civic.security.login', ['civic.security.login.form', 'civic.security.login.toolbar']);
(function() {
  'use strict';
  angular.module('civic.security.login.form', [])
    .controller('LoginFormController', LoginFormController);

  /**
   * @name LoginFormController
   * @desc provides the behaviour behind a reusable form to allow users to authenticate. This controller and
   * its template (login/form.tpl.html) are used in a modal dialog box by the security service.
   * @param $scope
   * @param security
   * @constructor
   * @ngInject
   */
  function LoginFormController($scope, Security, $location) {
    $scope.location = $location.absUrl();
    var url = $location.url();
    $scope.cancelLogin = function() {
      Security.cancelLogin(url);
    };
  }
})();

// Based loosely around work by Witold Szczerba - https://github.com/witoldsz/angular-http-auth
(function() {
  'use strict';
  angular.module('civic.security.service', [
    'civic.security.retryQueue',
    'civic.security.login',
    'dialogs.main'
  ])
    .factory('Security', Security);

// @ngInject
  function Security($http, $q, $location, RetryQueue, dialogs, $log) {
    // Redirect to the given url (defaults to '/')
    function redirect(url) {
      url = url || '/';
      $location.url(url);
    }

    // Login form dialog stuff
    var loginDialog = null;
    function openLoginDialog() {
      if ( loginDialog ) {
        throw new Error('Trying to open a dialog that is already open!');
      }
      loginDialog= dialogs.create('components/security/login/LoginForm.tpl.html','LoginFormController',{},'lg');
      loginDialog.result.then(onLoginDialogClose);
    }
    function closeLoginDialog(success) {
      $log.info('Security.closeLoginDialog() called.');
      if (loginDialog) {
        loginDialog.close(success);
      }
    }
    function onLoginDialogClose(success) {
      loginDialog = null;
      if ( success ) {
        RetryQueue.retryAll();
      } else {
        RetryQueue.cancelAll();
        // redirect();
      }
    }

    // Register a handler for when an item is added to the retry queue
    RetryQueue.onItemAddedCallbacks.push(function() {
      if ( RetryQueue.hasMore() ) {
        service.showLogin();
      }
    });

    // The public API of the service
    var service = {

      // Get the first reason for needing a login
      getLoginReason: function() {
        return RetryQueue.retryReason();
      },

      // Show the modal login dialog
      showLogin: function() {
        openLoginDialog();
      },

      // Attempt to authenticate a user by the given email and password
      login: function() {
        var request = $http.get('/api/current_user.json');
        return request.then(function(response) {
          service.currentUser = response.data.user;
          if ( service.isAuthenticated() ) {
            closeLoginDialog(true);
          }
          return service.isAuthenticated();
        });
      },

      // Give up trying to login and clear the retry queue
      cancelLogin: function(url) {
        closeLoginDialog(false);
        redirect(url);
      },

      // Logout the current user and redirect
      logout: function(redirectTo) {
        $http.get('/api/sign_out').then(function() { // success
          service.currentUser = null;
          redirect(redirectTo);
        }, function(response) { // failure
          $log.warn('COULD NOT LOG OUT' + JSON.stringify(response));
        });
      },

      // Ask the backend to see if a user is already authenticated - this may be from a previous session.
      requestCurrentUser: function() {
        if ( service.isAuthenticated() ) {
          return $q.when(service.currentUser);
        } else {
          return $http.get('/api/current_user.json').then(function(response) {
            // unauthenticated request returns an empty JSON object, so we count the keys
            // and if there are any we assume an authenticated user
            if(Object.keys(response.data).length > 0) {
              service.currentUser = response.data;
            } else {
              service.currentUser = null;
            }
            return service.currentUser;
          });
        }
      },

      // Information about the current user
      currentUser: null,

      // Is the current user authenticated?
      isAuthenticated: function(){
        return !!service.currentUser;
      },

      // Is the current user an editor?
      isEditor: function() {
        return !!(service.currentUser && service.currentUser.role === 'editor' ||
        service.currentUser && service.currentUser.role === 'admin');
      },

      // Is the current user an editor?
      isCurator: function() {
        return !!(service.currentUser && (service.currentUser.role === 'curator' || service.currentUser.role === 'editor'));
      }
    };

    return service;
  }
})();

(function() {
  'use strict';
  angular.module('civic.security.retryQueue', [])
    .factory('RetryQueue', RetryQueue);

  /**
   * @name RetryQueue
   * @desc This is a generic retry queue for security failures.  Each item is expected to expose two functions: retry and cancel.
   * @param $q
   * @param $log
   * @returns {{onItemAddedCallbacks: Array, hasMore: hasMore, push: push, pushRetryFn: pushRetryFn, retryReason: retryReason, cancelAll: cancelAll, retryAll: retryAll}}
   * @ngInject
   */
  function RetryQueue($q, $log) {
    var retryQueue = [];
    var service = {
      // The security service puts its own handler in here!
      onItemAddedCallbacks: [],

      hasMore: function() {
        return retryQueue.length > 0;
      },
      push: function(retryItem) {
        $log.info('retryQueue.push() called with item: ' + retryItem);
        retryQueue.push(retryItem);
        // Call all the onItemAdded callbacks
        angular.forEach(service.onItemAddedCallbacks, function(cb) {
          try {
            cb(retryItem);
          } catch(e) {
            $log.error('securityRetryQueue.push(retryItem): callback threw an error' + e);
          }
        });
      },
      pushRetryFn: function(reason, retryFn) {
        // The reason parameter is optional
        if ( arguments.length === 1) {
          retryFn = reason;
          reason = undefined;
        }

        // The deferred object that will be resolved or rejected by calling retry or cancel
        var deferred = $q.defer();
        var retryItem = {
          reason: reason,
          retry: function() {
            // Wrap the result of the retryFn into a promise if it is not already
            $q.when(retryFn()).then(function(value) {
              // If it was successful then resolve our deferred
              deferred.resolve(value);
            }, function(value) {
              // Othewise reject it
              deferred.reject(value);
            });
          },
          cancel: function() {
            // Give up on retrying and reject our deferred
            deferred.reject();
          }
        };
        service.push(retryItem);
        return deferred.promise;
      },
      retryReason: function() {
        return service.hasMore() && retryQueue[0].reason;
      },
      cancelAll: function() {
        while(service.hasMore()) {
          retryQueue.shift().cancel();
        }
      },
      retryAll: function() {
        $log.info('RetryQueue.retryall() called.');
        while(service.hasMore()) {
          retryQueue.shift().retry();
        }
      }
    };
    return service;
  }
})();
(function() {
  'use strict';
  angular.module('civic.security.interceptor', ['civic.security.retryQueue'])
    .factory('Interceptor', Interceptor);
    // .config(interceptorServiceConfig);

  /**
   * @name Interceptor
   * @desc listens for authentication failures
   * @param $injector
   * @param RetryQueue
   * @returns {Function}
   * @ngInject
   */
  function Interceptor($injector, RetryQueue) {
    return {
      response: function(response) { // success - just pass the response through
        return response;
      },
      responseError: function(response) {
        // The request bounced because it was not authorized - add a new request to the retry RetryQueue
        var $q = RetryQueue.pushRetryFn('unauthorized-server', function retryRequest() {
          // We must use $injector to get the $http service to prevent circular dependency
          return $injector.get('$http')(response.config);
        });
        return response || $q.when(response);
        //return response;
      }
    };

  }

  /**
   * @name interceptorServiceConfig
   * @desc We have to add the interceptor to the RetryQueue as a string because the interceptor depends upon service instances that are not available in the config block.
   * @param $httpProvider
   * @ngInject
   */
  //function interceptorServiceConfig($httpProvider) {
  //  // $httpProvider.interceptors.push('Interceptor');
  //}
})();

(function() {
  'use strict';
  angular.module('civic.security.authorization', ['civic.security.service'])
    // This service provides guard methods to support AngularJS routes.
    // You can add them as resolves to routes to require authorization levels
    // before allowing a route change to complete
    .provider('Authorization', {
      requireAdminUser: /*@ngInject*/ function(Authorization) {
        return Authorization.requireAdminUser();
      },

      requireAuthenticatedUser: /*@ngInject*/ function(Authorization) {
        return Authorization.requireAuthenticatedUser();
      },

      $get: /*@ngInject*/ function(Security, RetryQueue) {
        var service = {

          // Require that there is an authenticated user
          // (use this in a route resolve to prevent non-authenticated users from entering that route)
          requireAuthenticatedUser: function() {
            var promise = Security.requestCurrentUser().then(function() {
              if ( !Security.isAuthenticated() ) {
                return RetryQueue.pushRetryFn('unauthenticated-client', service.requireAuthenticatedUser);
              }
            });
            return promise;
          },

          // Require that there is an administrator logged in
          // (use this in a route resolve to prevent non-administrators from entering that route)
          requireAdminUser: function() {
            var promise = Security.requestCurrentUser().then(function() {
              if ( !Security.isEditor() ) {
                return RetryQueue.pushRetryFn('unauthorized-client', service.requireAdminUser);
              }
            });
            return promise;
          }

        };

        return service;
      }
    });

})();

(function() {
  'use strict';
  angular.module('civic.services')
    .factory('GenesResource', GenesResource)
    .factory('Genes', GenesService);

  // @ngInject
  function GenesResource($resource, $cacheFactory, _) {
    var cache = $cacheFactory.get('$http');

    var cacheInterceptor = function(response) {
      // console.log(['GenesResource: removing', response.config.url, 'from $http cache.'].join(' '));
      cache.remove(response.config.url);
      return response.$promise;
    };

    return $resource('/api/genes/:geneId',
      {
        geneId: '@geneId'
      },
      {
        // Base Gene Resources
        query: {
          method: 'GET',
          isArray: true,
          cache: cache
        },
        get: { // get a single gene
          method: 'GET',
          isArray: false,
          cache: cache
        },
        getName: { // get a single gene's name and entrez_id
          url: '/api/genes/:geneId',
          params: { detailed: 'false' },
          method: 'GET',
          isArray: false,
          cache: cache
        },
        update: {
          method: 'PATCH',
          interceptor: {
            response: cacheInterceptor
          }
        },
        delete: {
          method: 'DELETE',
          interceptor: {
            response: cacheInterceptor
          }
        },
        apply: {
          method: 'PATCH',
          cache: false
        },
        verify: {
          method: 'GET',
          url: '/api/genes/existence/:geneId',
          isArray: false,
          cache: cache
        },
        search: {

        },
        beginsWith: {
          method: 'GET',
          url: '/api/genes?name=:name&detailed=false',
          isArray: true,
          cache: cache
        },

        // Gene Additional Info
        getMyGeneInfo: {
          url: '/api/genes/:geneId/mygene_info_proxy',
          params: {
            geneId: '@geneId'
          },
          cache: cache,
          transformResponse: function(data) {
            if(typeof data === 'string') {
              data = JSON.parse(data);
            }
            var srcMap = {
              kegg: 'http://www.genome.jp/kegg-bin/show_pathway?',
              reactome: 'http://www.reactome.org/cgi-bin/control_panel_st_id?ST_ID=',
              pharmgkb: 'https://www.pharmgkb.org/pathway/',
              humancyc: 'http://humancyc.org/HUMAN/NEW-IMAGE?type=PATHWAY&object=',
              smpdb: 'http://www.smpdb.ca/view/',
              pid: 'http://pid.nci.nih.gov/search/pathway_landing.shtml?what=graphic&jpg=on&pathway_id=',
              wikipathways: 'http://wikipathways.org/index.php/Pathway:',
              netpath: null,
              biocarta: null,
              inoh: null,
              signalink: null,
              ehmn: null
            };
            var pathways = data.pathway || [];
            var pathwaysFinal = [];
            var link;
            for(var src in pathways){
              if(!angular.isArray(pathways[src])){
                pathways[src] = [pathways[src]];
              }
              for(var p in pathways[src]){
                link = srcMap[src]+pathways[src][p].id;
                if(srcMap[src] === null){
                  link = null;
                }
                pathwaysFinal.push({
                  name: pathways[src][p].name,
                  link: link,
                  src: src
                });
              }
            }
            data.pathway = pathwaysFinal;
            if(!_.isArray(data.alias) && data.alias){
              data.alias = [data.alias];
            }
            if(!_.isArray(data.interpro) && data.interpro){
              data.interpro = [data.interpro];
            }
            return data;
          }
        },

        // Gene Collections
        queryVariants: {
          method: 'GET',
          url: '/api/genes/:geneId/variants',
          isArray: true,
          cache: cache
        },
        queryVariantGroups: {
          method: 'GET',
          url: '/api/genes/:geneId/variant_groups',
          isArray: true,
          cache: cache
        },

        // Gene Comments Resources
        queryComments: {
          method: 'GET',
          url: '/api/genes/:geneId/comments',
          isArray: true,
          cache: cache
        },
        getComment: {
          method: 'GET',
          url: '/api/genes/:geneId/comments/:commentId',
          params: {
            geneId: '@geneId',
            commentId: '@commentId'
          },
          isArray: false,
          cache: cache
        },

        submitComment: {
          method: 'POST',
          url: '/api/genes/:geneId/comments',
          params: {
            geneId: '@geneId'
          },
          cache: false
        },
        updateComment: {
          method: 'PATCH',
          url: '/api/genes/:geneId/comments/:commentId',
          params: {
            geneId: '@geneId',
            commentId: '@commentId'
          },
          cache: false
        },
        deleteComment: {
          method: 'DELETE',
          url: '/api/genes/:geneId/comments/:commentId',
          params: {
            geneId: '@geneId',
            commentId: '@commentId'
          },
          cache: false
        }
      }
    );
  }

  // @ngInject
  function GenesService(GenesResource, $q, $cacheFactory) {
    var cache = $cacheFactory.get('$http');

    // Base Gene and Gene Collection
    var item = {};
    var collection = [];

    // Additional Gene Data
    var myGeneInfo = {};

    // Gene Collections
    var variants = [];
    var variantGroups = [];
    var comments = [];

    return {
      initBase: initBase,
      initComments: initComments,
      data: {
        item: item,
        collection: collection,
        myGeneInfo: myGeneInfo,
        variants: variants,
        variantGroups: variantGroups,
        comments: comments
      },

      // Gene Base
      query: query,
      get: get,
      getName: getName,
      update: update,
      delete: deleteItem,
      apply: apply,

      // Gene Additional Info
      getMyGeneInfo: getMyGeneInfo,

      // Verify
      verify: verify,

      // Gene Collections
      queryVariants: queryVariants,
      queryVariantGroups: queryVariantGroups,

      // Gene Comments
      queryComments: queryComments,
      getComment: getComment,
      submitComment: submitComment,
      updateComment: updateComment,
      deleteComment: deleteComment,

      // Misc
      beginsWith: beginsWith
    };

    function initBase(geneId) {
      return $q.all([
        get(geneId),
        getMyGeneInfo(geneId),
        queryVariants(geneId),
        queryVariantGroups(geneId)
      ]);
    }

    function initComments(geneId) {
      return $q.all([
        queryComments(geneId)
      ]);
    }
    // Gene Base
    function query() {
      return GenesResource.query().$promise
        .then(function(response) {
          angular.copy(response, collection);
          return response.$promise;
        });
    }
    function get(geneId) {
      return GenesResource.get({geneId: geneId}).$promise
        .then(function(response) {
          angular.copy(response, item);
          return response.$promise;
        });
    }
    function getName(geneId) {
      return GenesResource.getName({geneId: geneId}).$promise
        .then(function(response) {
          return response.$promise;
        });
    }
    function update(reqObj) {
      return GenesResource.update(reqObj).$promise
        .then(function(response) {
          angular.copy(response, item);
          return response.$promise;
        });
    }
    function deleteItem(geneId) {
      return GenesResource.delete({geneId: geneId}).$promise
        .then(function(response) {
          item = null;
          return response.$promise;
        });
    }
    function apply(reqObj) {
      return GenesResource.apply(reqObj).$promise.then(
        function(response) { // success
          cache.remove('/api/genes/' + response.id);
          get(reqObj.geneId);
          return $q.when(response);
        },
        function(error) { // fail
          return $q.reject(error);
        });
    }
    function verify(geneId) {
      return GenesResource.verify({geneId: geneId}).$promise
        .then(function(response) {
          return response.$promise;
        });
    }

    function beginsWith(name) {
      return GenesResource.beginsWith({name: name}).$promise
        .then(function(response) {
          return response.$promise;
        });
    }

    // Gene Additional Data
    function getMyGeneInfo(geneId) {
      return GenesResource.getMyGeneInfo({geneId: geneId}).$promise
        .then(function(response) {
          angular.copy(response, myGeneInfo);
          return response.$promise;
        });
    }

    // Gene Collections
    function queryVariants(geneId) {
      return GenesResource.queryVariants({geneId: geneId}).$promise
        .then(function(response) {
          angular.copy(response, variants);
          return response.$promise;
        });
    }
    function queryVariantGroups(geneId) {
      return GenesResource.queryVariantGroups({geneId: geneId}).$promise
        .then(function(response) {
          angular.copy(response, variantGroups);
          return response.$promise;
        });
    }

    // Gene Comments
    function queryComments(geneId) {
      return GenesResource.queryComments({geneId: geneId}).$promise
        .then(function(response) {
          angular.copy(response, comments);
          return response.$promise;
        });
    }
    function getComment(geneId, commentId) {
      return GenesResource.getComment({geneId: geneId, commentId: commentId}).$promise
        .then(function(response) {
          return response.$promise;
        });
    }
    function submitComment(reqObj) {
      return GenesResource.submitComment(reqObj).$promise
        .then(function(response) {
          cache.remove('/api/genes/' + reqObj.geneId + '/comments');
          queryComments(reqObj.geneId);
          return response.$promise;
        });
    }
    function updateComment(reqObj) {
      return GenesResource.updateComment(reqObj).$promise
        .then(function(response) {
          cache.remove('/api/genes/' + reqObj.geneId + '/comments');
          queryComments(reqObj.geneId);
          return response.$promise;
        });
    }
    function deleteComment(commentId) {
      return GenesResource.deleteComment({geneId: item.id, commentId: commentId}).$promise
        .then(function(response) {
          cache.remove('/api/genes/' + item.id + '/comments');
          queryComments(item.id);
          return response.$promise;
        });
    }
  }
})();

(function() {
  'use strict';
  angular.module('civic.services')
    .factory('GeneRevisionsResource', GeneRevisionsResource)
    .factory('GeneRevisions', GeneRevisionsService);

  function GeneRevisionsResource($resource, $cacheFactory) {
    var cache = $cacheFactory.get('$http');

    // adding this interceptor to a route will remove cached record
    var cacheResponseInterceptor = function(response) {
      cache.remove(response.config.url);
      return response.$promise;
    };

    return $resource('/api/genes/:geneId/suggested_changes/:revisionId',
      {
        geneId: '@geneId',
        revisionId: '@revisionId'
      },
      {
        // Base Gene Revisions Resources
        query: {
          method: 'GET',
          isArray: true,
          cache: cache
        },
        get: {
          method: 'GET',
          isArray: false,
          cache: cache
        },
        submitRevision: {
          method: 'POST',
          cache: false
        },
        acceptRevision: {
          method: 'POST',
          url: '/api/genes/:geneId/suggested_changes/:revisionId/accept',
          params: {
            geneId: '@geneId',
            revisionId: '@revisionId'
          },
          cache: false
        },
        rejectRevision: {
          method: 'POST',
          url: '/api/genes/:geneId/suggested_changes/:revisionId/reject',
          params: {
            geneId: '@geneId',
            revisionId: '@revisionId'
          },
          cache: false
        },

        // Gene Revisions Comments Resources
        submitComment: {
          method: 'POST',
          url: '/api/genes/:geneId/suggested_changes/:revisionId/comments',
          params: {
            geneId: '@geneId',
            revisionId: '@revisionId'
          },
          cache: false
        },
        updateComment: {
          method: 'PATCH',
          url: '/api/genes/:geneId/suggested_changes/:revisionId/comments/:commentId',
          params: {
            geneId: '@geneId',
            revisionId: '@revisionId',
            commentId: '@commentId'
          },
          interceptor: {
            response: cacheResponseInterceptor
          }
        },
        queryComments: {
          method: 'GET',
          url: '/api/genes/:geneId/suggested_changes/:revisionId/comments',
          params: {
            geneId: '@geneId',
            revisionId: '@revisionId'
          },
          isArray: true,
          cache: cache
        },
        getComment: {
          method: 'GET',
          url: '/api/genes/:geneId/suggested_changes/:revisionId/comments/:commentId',
          params: {
            geneId: '@geneId',
            revisionId: '@revisionId',
            commentId: '@commentId'
          },
          isArray: false,
          cache: cache
        },
        deleteComment: {
          method: 'DELETE',
          url: '/api/genes/:geneId/suggested_changes/:revisionId/comments/:commentId',
          params: {
            geneId: '@geneId',
            revisionId: '@revisionId',
            commentId: '@commentId'
          },
          cache: false
        }
      }
    );
  }

  function GeneRevisionsService(GeneRevisionsResource, Genes, $cacheFactory, $q) {
    // fetch genes cache, need to delete gene record when revision is submitted
    var cache = $cacheFactory.get('$http');

    // Base Gene Revision and Gene Revisions Collection
    var item = {};
    var parent = Genes.data.item;
    var collection = [];

    // Gene Revisions Comments
    var comment = {};
    var comments = [];

    return {
      initBase: initBase,
      initRevisions: initRevisions,
      initComments: initComments,
      data: {
        item: item,
        parent: parent,
        collection: collection,
        comment: comment,
        comments: comments
      },

      // Gene Revisions Base
      query: query,
      get: get,
      submitRevision: submitRevision,
      acceptRevision: acceptRevision,
      rejectRevision: rejectRevision,


      // Gene Revisions Comments
      queryComments: queryComments,
      getComment: getComment,
      submitComment: submitComment,
      updateComment: updateComment,
      deleteComment: deleteComment
    };

    function initBase(geneId, revisionId) {
      return $q.all([
        query(geneId, revisionId)
      ]);
    }

    function initRevisions(geneId) {
      return $q.all([
        query(geneId)
      ]);
    }

    function initComments(geneId, revisionId) {
      return $q.all([
        query(geneId, revisionId)
      ]);
    }

    // Gene Revisions Base
    function query(geneId) {
      return GeneRevisionsResource.query({ geneId: geneId }).$promise
        .then(function(response) {
          angular.copy(response, collection);
          return response.$promise;
        });
    }
    function get(geneId, revisionId) {
      return GeneRevisionsResource.get({ geneId: geneId, revisionId: revisionId }).$promise
        .then(function(response) {
          angular.copy(response, item);
          return response.$promise;
        });
    }

    function submitRevision(reqObj) {
      return GeneRevisionsResource.submitRevision(reqObj).$promise.then(
        function(response) { // success
          cache.remove('/api/genes/' + reqObj.id + '/suggested_changes');
          return $q.when(response);
        },
        function(error) { //fail
          return $q.reject(error);
        });
    }

    function acceptRevision(geneId, revisionId) {
      return GeneRevisionsResource.acceptRevision({ geneId: geneId, revisionId: revisionId }).$promise.then(
        function(response) {
          cache.remove('/api/genes/' + geneId + '/suggested_changes');
          query(geneId);
          cache.remove('/api/genes/' + geneId + '/suggested_changes/' + revisionId);
          get(geneId, revisionId);
          cache.remove('/api/genes/' + geneId );
          Genes.get(geneId);
          return $q.when(response);
        },
        function(error) {
          return $q.reject(error);
        });
    }
    function rejectRevision(geneId, revisionId) {
      return GeneRevisionsResource.rejectRevision({ geneId: geneId, revisionId: revisionId }).$promise.then(
        function(response) {
          cache.remove('/api/genes/' + response.id + '/suggested_changes');
          query(geneId);
          cache.remove('/api/genes/' + response.id + '/suggested_changes/' + revisionId);
          get(geneId, revisionId);
          return $q.when(response);
        },
        function(error) {
          return $q.reject(error);
        });
    }

    // Gene Revisions Comments
    function queryComments(geneId, revisionId) {
      return GeneRevisionsResource.queryComments({ geneId: geneId, revisionId: revisionId }).$promise
        .then(function(response) {
          angular.copy(response, comments);
          return response.$promise;
        });
    }
    function getComment(geneId, revisionId, commentId) {
      return GeneRevisionsResource.getComment({ geneId: geneId, revisionId: revisionId, commentId: commentId }).$promise
        .then(function(response) {
          angular.copy(response, comment);
          return response.$promise;
        });
    }
    function submitComment(reqObj) {
      return GeneRevisionsResource.submitComment(reqObj).$promise
        .then(function(response) {
          cache.remove('/api/genes/' + reqObj.geneId + '/suggested_changes/' + reqObj.revisionId + '/comments');
          queryComments(reqObj.geneId, reqObj.revisionId);
          return response.$promise;
        });
    }
    function updateComment(reqObj) {
      return GeneRevisionsResource.updateComment(reqObj).$promise
        .then(function(response) {
          angular.copy(response, comment);
          cache.remove('/api/genes/' + reqObj.geneId + '/suggested_changes/' + reqObj.revisionId + '/comments');
          cache.remove('/api/genes/' + reqObj.geneId + '/suggested_changes/' + reqObj.revisionId + '/comments/' + reqObj.commentId);
          queryComments(reqObj.geneId, reqObj.revisionId);
          return response.$promise;
        });
    }
    function deleteComment(commentId) {
      return GeneRevisionsResource.deleteComment({ geneId: parent.id, revisionId: item.id, commentId: commentId }).$promise
        .then(function(response) {
          cache.remove('/api/genes/' + parent.id + '/suggested_changes/' + item.id+ '/comments');
          queryComments(parent.id, item.id);
          return response.$promise;
        });
    }
  }
})();

