(function() {
  'use strict';
  angular.module('civic.services')
    .factory('VariantGroupsResource', VariantGroupsResource)
    .factory('VariantGroups', VariantGroupsService);

  // @ngInject
  function VariantGroupsResource($resource, $cacheFactory) {
    var cache = $cacheFactory.get('$http');

    var cacheInterceptor = function(response) {
      console.log(['VariantGroupsResource: removing', response.config.url, 'from $http cache.'].join(' '));
      cache.remove(response.config.url);
      return response;
    };

    return $resource('/api/variant_groups/:variantGroupId',
      {
        variantGroupId: '@variantGroupId'
      },
      {
        // Base VariantGroup Resources
        query: {
          method: 'GET',
          isArray: true,
          cache: cache
        },
        get: {
          method: 'GET',
          isArray: false,
          cache: cache
        },
        update: {
          method: 'PATCH',
          interceptor: {
            response: cacheInterceptor
          }
        },
        delete: {
          method: 'DELETE',
          interceptor: {
            response: cacheInterceptor
          }
        },
        apply: {
          method: 'PATCH'
        },
        add: {
          method: 'POST',
          cache: false
        },

        // VariantGroup Collections Resources
        queryVariants: {
          method: 'GET',
          url: '/api/variant_groups/:variantGroupId/variants',
          isArray: true,
          cache: cache
        },

        // VariantGroup Comments Resources
        queryComments: {
          method: 'GET',
          url: '/api/variant_groups/:variantGroupId/comments',
          isArray: true,
          cache: cache
        },
        getComment: {
          method: 'GET',
          url: '/api/variant_groups/:variantGroupId/comments/:commentId',
          params: {
            variantGroupId: '@variantGroupId',
            commentId: '@commentId'
          },
          isArray: false,
          cache: cache
        },

        submitComment: {
          method: 'POST',
          url: '/api/variant_groups/:variantGroupId/comments',
          params: {
            variantGroupId: '@variantGroupId'
          },
          cache: false
        },
        updateComment: {
          method: 'PATCH',
          url: '/api/variant_groups/:variantGroupId/comments/:commentId',
          params: {
            variantGroupId: '@variantGroupId',
            commentId: '@commentId'
          },
          cache: false
        },
        deleteComment: {
          method: 'DELETE',
          url: '/api/variant_groups/:variantGroupId/comments/:commentId',
          params: {
            variantGroupId: '@variantGroupId',
            commentId: '@commentId'
          },
          cache: false
        }
      }
    );
  }

  // @ngInject
  function VariantGroupsService(VariantGroupsResource, $q, $cacheFactory) {
    var cache = $cacheFactory.get('$http');
    // Base VariantGroup and VariantGroup Collection
    var item = {};
    var collection = [];

    // VariantGroup Collections
    var variants = [];
    var comments = [];

    return {
      initBase: initBase,
      initComments: initComments,
      data: {
        item: item,
        collection: collection,
        variants: variants,
        comments: comments
      },

      // VariantGroup Base
      query: query,
      get: get,
      update: update,
      delete: deleteItem,
      apply: apply,
      add: add,

      // VariantGroup Collections
      queryVariants: queryVariants,

      // VariantGroup Comments
      queryComments: queryComments,
      getComment: getComment,
      submitComment: submitComment,
      updateComment: updateComment,
      deleteComment: deleteComment

    };

    function initBase(variantGroupId) {
      return $q.all([
        get(variantGroupId),
        queryVariants(variantGroupId)
      ]);
    }

    function initComments(variantGroupId) {
      return $q.all([
        queryComments(variantGroupId)
      ]);
    }
    // VariantGroup Base
    function query() {
      return VariantGroupsResource.query().$promise
        .then(function(response) {
          angular.copy(response, collection);
          return response.$promise;
        });
    }
    function get(variantGroupId) {
      return VariantGroupsResource.get({variantGroupId: variantGroupId}).$promise
        .then(function(response) {
          angular.copy(response, item);
          angular.copy(response.variants, variants);
          return response.$promise;
        });
    }
    function update(reqObj) {
      return VariantGroupsResource.update(reqObj).$promise
        .then(function(response) {
          angular.copy(response, item);
          return response.$promise;
        });
    }
    function deleteItem(variantGroupId) {
      return VariantGroupsResource.delete({variantGroupId: variantGroupId}).$promise
        .then(function(response) {
          item = null;
          return response.$promise;
        });
    }
    function apply(reqObj) {
      return VariantGroupsResource.apply(reqObj).$promise.then(
        function(response) { // success
          cache.remove('/api/variant_groups/' + response.id);
          get(reqObj.variantGroupId);
          return $q.when(response);
        },
        function(error) { // fail
          return $q.reject(error);
        });
    }
    function add(reqObj) {
      return VariantGroupsResource.add(reqObj).$promise
        .then(function(response) {
          return response.$promise;
        });
    }

    // VariantGroup Collections
    function queryVariants() {
      console.warn('returning copy of variantGroups.variants');
      return $q.when(variants);
      //return VariantGroupsResource.queryVariants({variantGroupId: variantGroupId}).$promise
      //  .then(function(response) {
      //    angular.copy(response, variants);
      //    return response.$promise;
      //  });
    }

    // VariantGroup Comments
    function queryComments(variantGroupId) {
      return VariantGroupsResource.queryComments({variantGroupId: variantGroupId}).$promise
        .then(function(response) {
          angular.copy(response, comments);
          return response.$promise;
        });
    }
    function getComment(variantGroupId, commentId) {
      return VariantGroupsResource.getComment({variantGroupId: variantGroupId, commentId: commentId}).$promise
        .then(function(response) {
          return response.$promise;
        });
    }
    function submitComment(reqObj) {
      return VariantGroupsResource.submitComment(reqObj).$promise
        .then(function(response) {
          cache.remove('/api/variant_groups/' + reqObj.variantGroupId + '/comments');
          queryComments(reqObj.variantGroupId);
          return response.$promise;
        });
    }
    function updateComment(reqObj) {
      return VariantGroupsResource.updateComment(reqObj).$promise
        .then(function(response) {
          // TODO: delete comment from cache, refresh comments
          return response.$promise;
        });
    }
    function deleteComment(commentId) {
      return VariantGroupsResource.deleteComment({variantGroupId: item.id, commentId: commentId}).$promise
        .then(function(response) {
          cache.remove('/api/variant_groups/' + item.id + '/comments');
          queryComments(item.id);
          return response.$promise;
        });
    }
  }
})();

(function() {
  'use strict';
  angular.module('civic.services')
    .factory('VariantGroupRevisionsResource', VariantGroupRevisionsResource)
    .factory('VariantGroupRevisions', VariantGroupRevisionsService);

  function VariantGroupRevisionsResource($resource, $cacheFactory) {
    var cache = $cacheFactory.get('$http');

    // adding this interceptor to a route will remove cached record
    var cacheResponseInterceptor = function(response) {
      cache.remove(response.config.url);
      return response.$promise;
    };

    return $resource('/api/variant_groups/:variantGroupId/suggested_changes/:revisionId',
      {
        variantGroupId: '@variantGroupId',
        revisionId: '@revisionId'
      },
      {
        // Base VariantGroup Revisions Resources
        query: {
          method: 'GET',
          isArray: true,
          cache: cache
        },
        get: {
          method: 'GET',
          isArray: false,
          cache: cache
        },
        submitRevision: {
          method: 'POST',
          cache: false
        },
        acceptRevision: {
          method: 'POST',
          url: '/api/variant_groups/:variantGroupId/suggested_changes/:revisionId/accept',
          params: {
            variantGroupId: '@variantGroupId',
            revisionId: '@revisionId'
          },
          cache: false
        },
        rejectRevision: {
          method: 'POST',
          url: '/api/variant_groups/:variantGroupId/suggested_changes/:revisionId/reject',
          params: {
            variantGroupId: '@variantGroupId',
            revisionId: '@revisionId'
          },
          cache: cache
        },

        // VariantGroup Revisions Comments Resources
        submitComment: {
          method: 'POST',
          url: '/api/variant_groups/:variantGroupId/suggested_changes/:revisionId/comments',
          params: {
            variantGroupId: '@variantGroupId',
            revisionId: '@revisionId'
          },
          cache: false
        },
        updateComment: {
          method: 'PATCH',
          url: '/api/variant_groups/:variantGroupId/suggested_changes/:revisionId/comments/:commentId',
          params: {
            variantGroupId: '@variantGroupId',
            revisionId: '@revisionId',
            commentId: '@commentId'
          },
          interceptor: {
            response: cacheResponseInterceptor
          }
        },
        queryComments: {
          method: 'GET',
          url: '/api/variant_groups/:variantGroupId/suggested_changes/:revisionId/comments',
          params: {
            variantGroupId: '@variantGroupId',
            revisionId: '@revisionId'
          },
          isArray: true,
          cache: cache
        },
        getComment: {
          method: 'GET',
          url: '/api/variant_groups/:variantGroupId/suggested_changes/:revisionId/comments/:commentId',
          params: {
            variantGroupId: '@variantGroupId',
            revisionId: '@revisionId',
            commentId: '@commentId'
          },
          isArray: false,
          cache: cache
        },
        deleteComment: {
          method: 'DELETE',
          url: '/api/variant_groups/:variantGroupId/suggested_changes/:revisionId/comments/:commentId',
          params: {
            variantGroupId: '@variantGroupId',
            revisionId: '@revisionId',
            commentId: '@commentId'
          },
          cache: false
        }
      }
    );
  }

  function VariantGroupRevisionsService(VariantGroupRevisionsResource, VariantGroups, $cacheFactory, $q) {
    var cache = $cacheFactory.get('$http');

    // Base VariantGroup Revision and VariantGroup Revisions Collection
    var item = {};
    var parent = VariantGroups.data.item;
    var collection = [];

    // VariantGroup Revisions Comments
    var comment = {};
    var comments = [];

    return {
      initBase: initBase,
      initRevisions: initRevisions,
      initComments: initComments,
      data: {
        item: item,
        parent: parent,
        collection: collection,
        comment: comment,
        comments: comments
      },

      // VariantGroup Revisions Base
      query: query,
      get: get,
      submitRevision: submitRevision,
      acceptRevision: acceptRevision,
      rejectRevision: rejectRevision,

      // VariantGroup Revisions Comments
      queryComments: queryComments,
      getComment: getComment,
      submitComment: submitComment,
      updateComment: updateComment,
      deleteComment: deleteComment,

    };

    function initBase(variantGroupId, revisionId) {
      return $q.all([
        query(variantGroupId, revisionId)
      ]);
    }

    function initRevisions(variantGroupId) {
      return $q.all([
        query(variantGroupId)
      ]);
    }

    function initComments(variantGroupId, revisionId) {
      return $q.all([
        query(variantGroupId, revisionId)
      ]);
    }

    // VariantGroup Revisions Base
    function query(variantGroupId) {
      return VariantGroupRevisionsResource.query({ variantGroupId: variantGroupId }).$promise
        .then(function(response) {
          angular.copy(response, collection);
          return response.$promise;
        });
    }
    function get(variantGroupId, revisionId) {
      return VariantGroupRevisionsResource.get({ variantGroupId: variantGroupId, revisionId: revisionId }).$promise
        .then(function(response) {
          angular.copy(response, item);
          return response.$promise;
        });
    }

    function submitRevision(reqObj) {
      return VariantGroupRevisionsResource.submitRevision(reqObj).$promise.then(
        function(response) { // success
          cache.remove('/api/variant_groups/' + reqObj.id + '/suggested_changes');
          return $q.when(response);
        },
        function(error) { //fail
          return $q.reject(error);
        });
    }

    function acceptRevision(variantGroupId, revisionId) {
      return VariantGroupRevisionsResource.acceptRevision({ variantGroupId: variantGroupId, revisionId: revisionId }).$promise.then(
        function(response) {
          cache.remove('/api/variant_groups/' + variantGroupId + '/suggested_changes');
          cache.remove('/api/variant_groups/' + variantGroupId + '/suggested_changes/' + revisionId);
          cache.remove('/api/variant_groups/' + variantGroupId );
          query(variantGroupId);
          get(variantGroupId, revisionId);
          VariantGroups.get(variantGroupId);
          return $q.when(response);
        },
        function(error) {
          return $q.reject(error);
        });
    }
    function rejectRevision(variantGroupId, revisionId) {
      return VariantGroupRevisionsResource.rejectRevision({ variantGroupId: variantGroupId, revisionId: revisionId }).$promise.then(
        function(response) {
          cache.remove('/api/variant_groups/' + response.id + '/suggested_changes');
          query(variantGroupId);
          cache.remove('/api/variant_groups/' + response.id + '/suggested_changes/' + revisionId);
          get(variantGroupId, revisionId);
          return $q.when(response);
        },
        function(error) {
          return $q.reject(error);
        });
    }

    // VariantGroup Revisions Comments
    function queryComments(variantGroupId, revisionId) {
      return VariantGroupRevisionsResource.queryComments({ variantGroupId: variantGroupId, revisionId: revisionId }).$promise
        .then(function(response) {
          angular.copy(response, comments);
          return response.$promise;
        });
    }
    function getComment(variantGroupId, revisionId, commentId) {
      return VariantGroupRevisionsResource.getComment({ variantGroupId: variantGroupId, revisionId: revisionId, commentId: commentId }).$promise
        .then(function(response) {
          angular.copy(response, comment);
          return response.$promise;
        });
    }
    function submitComment(reqObj) {
      return VariantGroupRevisionsResource.submitComment(reqObj).$promise
        .then(function(response) {
          cache.remove('/api/variant_groups/' + reqObj.variantGroupId + '/suggested_changes/' + reqObj.revisionId + '/comments');
          queryComments(reqObj.variantGroupId, reqObj.revisionId);
          return response.$promise;
        });
    }
    function updateComment(reqObj) {
      return VariantGroupRevisionsResource.updateComment(reqObj).$promise
        .then(function(response) {
          cache.remove('/api/variant_groups/' + reqObj.variantGroupId + '/suggested_changes/' + reqObj.revisionId + '/comments');
          return response.$promise;
        });
    }
    function deleteComment(commentId) {
      return VariantGroupRevisionsResource.deleteComment({ variantGroupId: parent.id, revisionId: item.id, commentId: commentId }).$promise
        .then(function(response) {
          cache.remove('/api/variant_groups/' + parent.id + '/suggested_changes/' + item.id + '/comments');
          queryComments(parent.id, item.id);
          return response.$promise;
        });
    }
  }
})();

