(function() {
  'use strict';
  angular.module('civic.services')
    .factory('MyGeneInfoResource', MyGeneInfoResource)
    .factory('MyGeneInfo', MyGeneInfoService);

  // @ngInject
  function MyGeneInfoResource($resource, $cacheFactory, _) {

    var cache = $cacheFactory('MyGeneInfo'); // default cache doesn't work for some reason

    return $resource('/api/genes/mygene_info_proxy/:geneId',
      {
        geneId: '@geneId'
      },
      {
        get: {
          // isArray: false,
          cache: cache,
          transformResponse: function(data) {
            if(typeof data === 'string') {
              data = JSON.parse(data);
            }
            var srcMap = {
              kegg: 'http://www.genome.jp/kegg-bin/show_pathway?',
              reactome: 'http://www.reactome.org/cgi-bin/control_panel_st_id?ST_ID=',
              pharmgkb: 'https://www.pharmgkb.org/pathway/',
              humancyc: 'http://humancyc.org/HUMAN/NEW-IMAGE?type=PATHWAY&object=',
              smpdb: 'http://www.smpdb.ca/view/',
              pid: 'http://pid.nci.nih.gov/search/pathway_landing.shtml?what=graphic&jpg=on&pathway_id=',
              wikipathways: 'http://wikipathways.org/index.php/Pathway:',
              netpath: null,
              biocarta: null,
              inoh: null,
              signalink: null,
              ehmn: null
            };
            var pathways = data.pathway || [];
            var pathwaysFinal = [];
            var link;
            for(var src in pathways){
              if(!angular.isArray(pathways[src])){
                pathways[src] = [pathways[src]];
              }
              for(var p in pathways[src]){
                link = srcMap[src]+pathways[src][p].id;
                if(srcMap[src] === null){
                  link = null;
                }
                pathwaysFinal.push({
                  name: pathways[src][p].name,
                  link: link,
                  src: src
                });
              }
            }
            data.pathway = pathwaysFinal;
            if(!_.isArray(data.alias) && data.alias){
              data.alias = [data.alias];
            }
            if(!_.isArray(data.interpro) && data.interpro){
              data.interpro = [data.interpro];
            }
            return data;
          }
        }

      });
  }

  // @ngInject
  function MyGeneInfoService(MyGeneInfoResource) {
    return {
      get: function(entrez_id) {
        return MyGeneInfoResource.get({geneId: entrez_id}).$promise
          .then(function(response) {
            return response;
        });
      }
    };
  }
})();

(function() {
  'use strict';
  angular.module('civic.services')
    .service('TitleService', TitleService);

  // @ngInject
  // Titles are constructed by parsing the state's data.titleExp string within a scope constructed
  // of the relevant CIViC entities (gene, variant, evidenceItem, variantGroup)
  function TitleService($rootScope, Genes, Variants, Evidence, VariantGroups, Users, _, $q, $parse) {
    $rootScope.$on('$stateChangeSuccess',function(event, toState, toParams) {
      var title = '';

      var titleScope = {
        gene: {},
        variant: {},
        evidence: {},
        variantGroup: {},
        user: {}
      };

      var genePromise, variantPromise, variantGroupsPromise, evidencePromise, usersPromise;

      // TODO: get needed entity resources by querying $state.$current.globals
      // note: requires that all resources necessary for parsing a state's titleExp must be resolved by ui-router state config

      // construct promises for relevant entities
      if(_.has(toParams, 'geneId') && titleScope.gene.entrez_id !== toParams.geneId) {
        genePromise = Genes.get(toParams.geneId);
      }

      if(_.has(toParams, 'variantId') && titleScope.variant.id !== toParams.variantId) {
        variantPromise = Variants.get(toParams.variantId);
      }

      if(_.has(toParams, 'variantGroupId') && titleScope.variantGroup.id !== toParams.variantGroupId) {
        variantGroupsPromise = VariantGroups.get(toParams.variantGroupId);
      }

      if(_.has(toParams, 'evidenceId') && titleScope.evidence.id !== toParams.evidenceId) {
        evidencePromise = Evidence.get(toParams.evidenceId);
      }

      if(_.has(toParams, 'userId') && titleScope.user.id !== toParams.userId) {
        usersPromise = Users.get(toParams.userId);
      }

      // resolve promises, apply $parse with constructed title scope
      $q.all({
        gene: genePromise,
        variant: variantPromise,
        evidence: evidencePromise,
        variantGroup: variantGroupsPromise,
        user: usersPromise})
        .then(function(resolutions) {
          titleScope.gene = resolutions.gene;
          titleScope.variant = resolutions.variant;
          titleScope.evidence = resolutions.evidence;
          titleScope.variantGroup = resolutions.variantGroup;
          titleScope.user = resolutions.user;

          title = $parse(toState.data.titleExp)(titleScope);

          $rootScope.$broadcast('title:update', { newTitle: title });
        });
    });
  }

})();

(function() {
  'use strict';
  angular.module('civic.services')
    .factory('_', function ($window) {
      return $window._;
    });
})();
(function (ng, _) {
  'use strict';

  var
    lodashModule = ng.module('angular-lodash', []),
    utilsModule = ng.module('angular-lodash/utils', []),
    filtersModule = ng.module('angular-lodash/filters', []);

  // begin custom _

  function propGetterFactory(prop) {
    return function(obj) {return obj[prop];};
  }

  _._ = _;

  // Shiv "min", "max" ,"sortedIndex" to accept property predicate.
  _.each(['min', 'max', 'sortedIndex'], function(fnName) {
    _[fnName] = _.wrap(_[fnName], function(fn) {
      var args = _.toArray(arguments).slice(1);

      if(_.isString(args[2])) {
        // for "sortedIndex", transmuting str to property getter
        args[2] = propGetterFactory(args[2]);
      }
      else if(_.isString(args[1])) {
        // for "min" or "max", transmuting str to property getter
        args[1] = propGetterFactory(args[1]);
      }

      return fn.apply(_, args);
    });
  });

  // Shiv "filter", "reject" to angular's built-in,
  // and reserve lodash's feature(works on obj).
  ng.injector(['ng']).invoke(['$filter', function($filter) {
    _.filter = _.select = _.wrap($filter('filter'), function(filter, obj, exp) {
      if(!(_.isArray(obj))) {
        obj = _.toArray(obj);
      }

      return filter(obj, exp);
    });

    _.reject = function(obj, exp) {
      // use angular built-in negated predicate
      if(_.isString(exp)) {
        return _.filter(obj, '!' + exp);
      }

      var diff = _.bind(_.difference, _, obj);

      return diff(_.filter(obj, exp));
    };
  }]);

  // end custom _


  // begin register angular-lodash/utils

  _.each(_.methods(_), function(methodName) {
    function register($rootScope) {$rootScope[methodName] = _.bind(_[methodName], _);}

    _.each([
      lodashModule,
      utilsModule,
      ng.module('angular-lodash/utils/' + methodName, [])
    ], function(module) {
      module.run(['$rootScope', register]);
    });
  });

  // end register angular-lodash/utils


  // begin register angular-lodash/filters

  var
    adapList = [
      ['map', 'collect'],
      ['reduce', 'inject', 'foldl'],
      ['reduceRight', 'foldr'],
      ['find', 'detect'],
      ['filter', 'select'],
      'where',
      'findWhere',
      'reject',
      'invoke',
      'pluck',
      'max',
      'min',
      'sortBy',
      'groupBy',
      'countBy',
      'shuffle',
      'toArray',
      'size',
      ['first', 'head', 'take'],
      'initial',
      'last',
      ['rest', 'tail', 'drop'],
      'compact',
      'flatten',
      'without',
      'union',
      'intersection',
      'difference',
      ['uniq', 'unique'],
      'zip',
      'object',
      'indexOf',
      'lastIndexOf',
      'sortedIndex',
      'keys',
      'values',
      'pairs',
      'invert',
      ['functions', 'methods'],
      'pick',
      'omit',
      'tap',
      'identity',
      'uniqueId',
      'escape',
      'result',
      'template'
    ];

  _.each(adapList, function(filterNames) {
    if(!(_.isArray(filterNames))) {
      filterNames = [filterNames];
    }

    var
      filter = _.bind(_[filterNames[0]], _),
      filterFactory = function() {return filter;};

    _.each(filterNames, function(filterName) {
      _.each([
        lodashModule,
        filtersModule,
        ng.module('angular-lodash/filters/' + filterName, [])
      ], function(module) {
        module.filter(filterName, filterFactory);
      });
    });
  });

  // end register angular-lodash/filters

}(angular, window._));

