(function() {
  'use strict';
  angular.module('civic.users.profile')
    .controller('ProfileEditController', ProfileEditController);

  // @ngInject
  function ProfileEditController ($scope,
                                  $state,
                                  $interval,
                                  Security,
                                  Users,
                                  user){
    var vm = $scope.vm = {};

    vm.user = user;
    vm.userEdit = angular.copy(user);
    vm.currentUser = Security.currentUser;
    vm.secondsReturn = 5;

    // TODO: implement better error handling and success message
    vm.submitSuccess = false;
    vm.submitFail = false;

    vm.userEditFields = [
      {
        key: 'name',
        type: 'horizontalInputHelp',
        templateOptions: {
          label: 'Name',
          minLength: 8,
          value: 'vm.userEdit.name',
          helpText: 'Name'
        }
      },
      {
        key: 'username',
        type: 'horizontalInputHelp',
        templateOptions: {
          label: 'User Name',
          minLength: 8,
          value: 'vm.userEdit.user',
          helpText: 'Username'
        }
      },
      {
        key: 'email',
        type: 'horizontalInputHelp',
        templateOptions: {
          label: 'Email Address',
          minLength: 5,
          value: 'vm.userEdit.email',
          helpText: 'Email address will be used for sending CIViC notifications, news, and updates.'
        }
      },
      {
        key: 'area_of_expertise',
        type: 'horizontalSelectHelp',
        templateOptions: {
          label: 'Areas of Expertise',
          options: [
            { value: null, label: 'Please select an Area of Expertise' },
            { value: 'Patient Advocate', label: 'Patient Advocate'},
            { value: 'Clinical Scientist', label: 'Clinical Scientist' },
            { value: 'Research Scientist', label: 'Research Scientist' }
          ],
          valueProp: 'value',
          labelProp: 'label',
          value: 'vm.userEdit.area_of_expertise',
          helpText: 'Area of Expertise'
        }
      }
    ];

    vm.saveProfile = function(userEdit) {

      Users.update(userEdit)
        .then(function(response) {
          console.log('updated user successfully');
          vm.user = Security.currentUser = response;
          vm.submitSuccess = true;
          vm.count = vm.secondsReturn;
          var int = $interval(function() {
            if (vm.count <= 1) {
              $state.go('users.profile', { userId: user.id });
              $interval.cancel(int);
            } else {
              vm.count--;
            }
          }, 1000);

        })
        .catch(function() {
          console.error('update user error!');
          vm.submitFail = false;
        });

    };
  }
})();

(function() {
  'use strict';
  angular.module('civic.users')
    .controller('UserSummaryController', UserSummaryController)
    .directive('userSummary', function() {
      return {
        restrict: 'E',
        scope: {
          user: '='
        },
        controller: 'UserSummaryController',
        templateUrl: 'app/views/users/common/userSummary.tpl.html'
      };
    });

  // @ngInject
  function UserSummaryController($scope, Stats, Security) {
    var vm = $scope.vm = {};
    vm.currentUser= Security.currentUser;
    vm.stats = {};

    Stats.user($scope.user.id).then(function(stats) {
      vm.stats = stats;
    });
  }
})();

(function(module) {
try {
  module = angular.module('civicClient');
} catch (e) {
  module = angular.module('civicClient', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('app/pages/apiDocumentation.tpl.html',
    '<div class="row"><div class="col-xs-12 col-xs-offset-0 col-md-10 col-md-offset-1 col-lg-8 col-lg-offset-2"><h3>CIViC API Documentation</h3><p>CIViC provides a simple API that can be utilized over HTTP in the programming language of your choice or even at the command line.</p><p>The CIViC API attempts to be RESTful whenever appropriate which hopefully makes it intuitive to use. There are four main entities in the system that a user may interact with: Genes, Variants, Variant Groups, and Evidence Items. These entities form a hierarchy that is reflected in the API endpoints. A Gene has one or more Variants, each of which may have one or more Evidence Items. Variants may also be collected into Variant Groups.</p><br><h4>Genes</h4><hr><pre>\n' +
    '<strong>GET</strong> /api/genes\n' +
    '<strong>GET</strong> /api/genes/:gene_id\n' +
    '    </pre><p>The genes endpoint can be used to get either a list of all genes in the system, or more detailed information for a single gene using a gene_id in the url string. As an example, here is (clickable) sample request for the gene "BRAF" (gene_id: 5)</p><pre>\n' +
    '<strong>GET</strong> <a href="/api/genes/5" target="_blank">/api/genes/5</a>\n' +
    '    </pre><p>You can always use the API call one level up the hierarchy to retrieve a list of IDs for the entity you are interested in. For instance, the genes endpoint will return a list of Variant IDs for that Gene.</p><br><h4>Variants</h4><hr><pre>\n' +
    '<strong>GET</strong> /api/genes/:gene_id/variants\n' +
    '    </pre><p>The variants endpoint can be used to retrieve a list of all variants for a single gene (using an Entrez ID), or a more detailed view of a specific variant (using the Entrez ID and variant ID).</p><pre>\n' +
    '<strong>GET</strong> <a href="/api/variants/5" target="_blank">/api/variants/5</a>\n' +
    '    </pre><br><h4>Evidence Items</h4><hr><p>The variants endpoint may retrieve a list of evidence items linked to a variant:</p><pre>\n' +
    '<strong>GET</strong> /api/variants/:variant_id/evidence_items\n' +
    '    </pre><p>The evidence items endpoint may then be used to retrieve a more detailed view of a specific evidence_item:</p><pre>\n' +
    '<strong>GET</strong> <a href="/api/evidence_items/9" target="_blank">/api/evidence_items/9</a>\n' +
    '    </pre><br><h4>Other Features</h4><hr><pre>\n' +
    '<strong>GET</strong> /api/genes/:gene_id/comments\n' +
    '<strong>GET</strong> /api/genes/:gene_id/revisions\n' +
    '<strong>GET</strong> /api/genes/:gene_id/suggested_changes\n' +
    '<strong>GET</strong> /api/genes/:gene_id/suggested_changes/:change_id/comments\n' +
    '    </pre><p>Each of the four main entities in the system (Genes, Variants, Variant Groups, and Evidence Items) also support additional endpoints for comments, version history, and suggested changes. Suggested changes can also have comments themselves. These endpoints can be accessed by adding the appropriate resource name (comments, revisions, suggested_changes) to the end of the route for the entity that you are interested in. For example, to get a list of revisions for the gene "BRAF":</p><pre>\n' +
    '<strong>GET</strong> <a href="/api/genes/5/revisions" target="_blank">/api/genes/5/revisions</a>\n' +
    '    </pre><p>We are working on additional API support and documentation. Soon, you will be able to do anything you do from the site (add comments, suggest new evidence items, etc) directly from the API.</p></div></div>');
}]);
})();

