(function(module) {
try {
  module = angular.module('civicClient');
} catch (e) {
  module = angular.module('civicClient', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('app/pages/collaborate.tpl.html',
    '<div class="row" style="padding-top: 16px;"><div class="col-xs-12 col-xs-offset-0 col-md-10 col-md-offset-1 col-lg-8 col-lg-offset-2"><div class="row"><div class="col-xs-6"><h3 class="col-title">Why CIViC?</h3><p>CIViC is an open access, open source, community-driven web resource for Clinical Interpretation of Variants in Cancer. Our goal is to enable precision medicine by providing an educational forum for dissemination of knowledge and active discussion of the clinical significance of cancer genome alterations.</p><h3 class="col-title">How you can contribute:</h3><p>There are several mechanisms by which you can make a contribution to this important problem:</p><ol><li><strong>Consume</strong><br>Make use of the community-created content in your own research. All CIViC data and source code are provided freely for almost any use.</li><li><strong>Comment</strong><br>Participate in an ongoing discussion in an effort to reach community consensus on the appropriate clinical action(s) for a genomic event.</li><li><strong>Correct</strong><br>Submit a correction to any details about a genomic event, evidence statement or interpretation.</li><li><strong>Create</strong><br>Propose a new genomic variant (single nucleotide substitution, structural variant, gene fusion, etc), add evidence statements that support clinical actions assocated with such variants, or help create a synthesized interpretation or summary of the corpus of evidence for a variant.</li></ol><p>Before commenting, correcting, or creating please visit the <a ui-sref="help">help</a> and <a ui-sref="faq">FAQ</a> pages to learn more about the CIViC data model and <a ui-sref="browse">browse the existing content</a> for examples.</p></div><div class="col-xs-6"><h3 class="col-title">CIViC principles</h3><ul><li>We believe that the interpretations of clinical actionability required to enable precision medicine should be freely available and openly discussed across a diverse community.</li><li>An interdisciplinary approach is needed to combine the expertise of genome scientists and health care providers, whose efforts are often isolated.</li><li>Content should be created with transparency, kept current, be comprehensive, track provenance, and acknowledge the efforts of creators.</li><li>The interface should be both structured enough to allow computational data mining and agile enough to handle the product of openly-debated, human interpretation.</li><li>CIViC is committed to provide unencumbered and efficient access to community-driven curation and interpretation of cancer mutations. Curated knowledge will remain free and can be accessed anonymously without login unless the user wishes to contribute to content.</li><li>To encourage both academic and commercial engagement CIViC is released with minimal restrictions under a Creative Commons license (<a href="https://creativecommons.org/licenses/by/4.0/">CC BY 4.0</a>). No fees or exclusive access will be introduced. While sharing improvements is strongly encouraged, the data can be adopted and used for nearly any purpose including the creation of commercial applications derived from the knowledge. We require only that attribution be given to the community that created the content.</li></ul></div></div></div></div>');
}]);
})();

