module.run(['$templateCache', function($templateCache) {
  $templateCache.put('app/pages/help.tpl.html',
    '<div class="helpPage"><div class="row"><div class="col-xs-12 col-xs-offset-0 col-md-10 col-md-offset-1 col-lg-8 col-lg-offset-2"><h2 style="margin-top: 0">Introduction to the CIViC Interface</h2><p>To learn more about the presentation of the information in CIViC, watch this video tutorial. This is the first installment in a four part series of the main CIViC functionality. Stay tuned for more videos!</p><div class="embed-responsive embed-responsive-16by9"><iframe class="embed-responsive-item" src="https://www.youtube.com/embed/KImI_oNYtyc" frameborder="0" allowfullscreen=""></iframe></div><h3>Understanding the CIViC Data Model</h3><p>At the heart of CIViC is the clinical evidence statement. The clinical evidence statement is a piece of information that has been manually curated from trustable medical literature about a variant or genomic \'event\' that has implications in cancer diagnosis, prognosis, or predictive response to therapy. For example, “Patients with BRAF V600 mutations respond well to the drug dabrafenib”. A variant may be a single nucleotide substitution, a small insertion or deletion, a chromosomal rearrangement, an RNA gene fusion, an RNA expression pattern (e.g. over-expression), and others. Each clinical evidence statement corresponds to a single citable publication.</p><tabset><tab ng-repeat="tab in vm.tabs" heading="{{ tab.heading }}" active="tab.active"><div ng-include="tab.template"></div></tab></tabset></div></div></div>');
}]);
